.content-spotlight {
  background-color: #000;
  height: 48rem;
  position: relative;
  overflow: hidden;
}

.content-spotlight--slider {
  overflow: hidden;
}

.content-spotlight--main-content {
  width: 100%;
  transition: transform 1s;
  position: absolute;
  top: 0;
}

.content-spotlight--backdrop {
  object-fit: cover;
  width: 100%;
  height: 48rem;
}

.content-spotlight--title {
  color: var(--c-white);
  font-size: var(--fs-large-l);
  text-transform: uppercase;
  text-shadow: 0 0 .4rem var(--c-black);
  margin-left: var(--padding-browser-side-extra);
  font-weight: 600;
  position: absolute;
  bottom: 14.4%;
}

.content-spotlight--more-container {
  color: var(--c-white);
  font-size: var(--fs-med-m);
  text-shadow: 0 0 .4rem var(--c-black);
  align-items: center;
  gap: .4rem;
  margin-left: 7.4rem;
  display: flex;
  position: absolute;
  bottom: 6.5rem;
}

.content-spotlight--more-container:hover {
  cursor: pointer;
  text-decoration: underline;
}

.content-spotlight--more-icon {
  width: 1.6rem;
  height: 1.6rem;
}

.content-spotlight--overview {
  color: var(--c-white);
  padding: 1rem var(--padding-browser-side) 1rem var(--padding-browser-side);
  background-color: #000000bd;
  border-radius: 0 .6rem 0 0;
  max-width: 55rem;
  height: 24rem;
  transition: transform .2s ease-in;
  position: absolute;
  bottom: 0;
  transform: translateX(-100%);
}

.content-spotlight--overview > div:first-child {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.content-spotlight--overview > div > div {
  align-items: center;
  gap: 1.2rem;
  display: flex;
}

.content-spotlight--overview-rating {
  background-color: var(--c-rating-okay);
  color: var(--c-black);
  font-size: var(--fs-med-m);
  border-radius: .3rem;
  justify-content: center;
  align-items: center;
  width: 3rem;
  height: 2rem;
  font-weight: 600;
  display: flex;
}

.content-spotlight--overview-back-btn {
  color: var(--c-white);
  background: none;
  border: none;
  width: 3.2rem;
  height: 3.2rem;
}

.content-spotlight--overview-back-btn:hover {
  cursor: pointer;
}

.content-spotlight--overview-back-btn-icon {
  width: 2.8rem;
  height: 2.8rem;
}

.content-spotlight--overview-title {
  font-size: var(--fs-large-m);
  text-transform: uppercase;
  font-weight: 600;
}

.content-spotlight--overview-date {
  margin-left: .1rem;
  position: relative;
  bottom: .8rem;
}

.content-spotlight--overview-genres {
  margin-bottom: 1rem;
  margin-left: .04rem;
  display: flex;
  position: relative;
  bottom: .8rem;
}

.content-spotlight--overview-description {
  font-size: var(--fs-med-m);
  line-clamp: 5;
  -webkit-line-clamp: 5;
  -webkit-box-orient: vertical;
  width: 48rem;
  height: 11rem;
  display: -webkit-box;
  overflow: hidden;
}

.content-spotlight--btn {
  z-index: 50;
  background: none;
  border: none;
  width: max-content;
  height: max-content;
  transition: transform .25s;
  position: absolute;
}

.content-spotlight--btn:hover {
  cursor: pointer;
  transform: scale(1.27);
}

.content-spotlight--btn:focus {
  outline: none;
}

.content-spotlight--left-btn {
  bottom: 50%;
  left: 2.3%;
}

.content-spotlight--right-btn {
  bottom: 50%;
  right: 2.3%;
}

.content-spotlight--btn-icon {
  color: var(--c-white);
  width: 3.6rem;
  height: 3.6rem;
}

.slider-shadow-right {
  z-index: 49;
  background-color: #0003;
  width: 0;
  height: 48rem;
  transition: all .3s ease-out;
  position: absolute;
  bottom: 0;
  right: 0;
}

.slider-shadow-left {
  z-index: 49;
  background-color: #0003;
  width: 0;
  height: 48rem;
  transition: all .3s ease-out;
  position: absolute;
  bottom: 0;
}

.content-spotlight--markers {
  align-items: center;
  gap: .8rem;
  display: flex;
  position: absolute;
  bottom: 2%;
  right: 50%;
}

.content-spotlight--marker {
  background-color: var(--c-white);
  border-radius: .2rem;
  width: .8rem;
  height: .8rem;
  transition: all .3s ease-in;
}

.content-spotlight--marker:hover {
  cursor: pointer;
}

.content-spotlight--marker-active {
  background-color: var(--main-c-lightest);
  width: 1.2rem;
  height: 1.2rem;
}

